import { useState } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { useQuery, useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';

import { Settings, Users, MessageCircle } from 'lucide-react';
import { motion } from 'framer-motion';

export default function AdminSettings() {
    const { user } = useAuth();
    const [isUpdating, setIsUpdating] = useState(false);

    // Queries
    const globalPostCreationEnabled = useQuery(api.settings.getUserPostCreationEnabled);
    const commentsEnabled = useQuery(api.settings.getCommentsEnabled);
    const questionsOnPostsEnabled = useQuery(api.settings.getQuestionsOnPostsEnabled);
    const chatEnabled = useQuery(api.settings.getChatEnabled);

    // Mutations
    const togglePostCreation = useMutation(api.settings.toggleUserPostCreation);
    const toggleComments = useMutation(api.settings.toggleCommentsEnabled);
    const toggleQuestionsOnPosts = useMutation(api.settings.toggleQuestionsOnPostsEnabled);
    const toggleChat = useMutation(api.settings.toggleChatEnabled);

    const handleToggle = async (
        mutation: any,
        currentValue: boolean | undefined,
        key: string
    ) => {
        if (!user || currentValue === undefined) return;

        setIsUpdating(true);
        try {
            await mutation({
                adminId: user._id,
                enabled: !currentValue,
            });
        } catch (error) {
            console.error(`Failed to update ${key}:`, error);
            alert("Failed to update setting. Please try again.");
        } finally {
            setIsUpdating(false);
        }
    };

    if (!user) return null;

    return (
        <div className="min-h-screen bg-light-bg dark:bg-dark-bg pb-20 md:pb-8">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8 max-w-4xl pt-6">
                {/* Page Header */}
                <div className="flex items-center gap-3 mb-6">
                    <div className="p-3 rounded-xl bg-gradient-to-br from-primary-500/10 to-secondary-500/10">
                        <Settings className="w-6 h-6 text-primary-600 dark:text-primary-400" />
                    </div>
                    <div>
                        <h1 className="text-2xl font-bold text-light-text dark:text-dark-text font-display">
                            System Settings
                        </h1>
                        <p className="text-sm text-light-muted dark:text-dark-muted">
                            Manage global permissions and system configurations
                        </p>
                    </div>
                </div>

                {/* Settings Sections */}
                <div className="space-y-6">

                    {/* User Permissions Section */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="glass rounded-2xl p-6 shadow-lg space-y-6"
                    >
                        <div className="flex items-center gap-3 mb-4">
                            <Users className="w-5 h-5 text-primary-600 dark:text-primary-400" />
                            <h2 className="text-lg font-bold text-light-text dark:text-dark-text">
                                Global Controls
                            </h2>
                        </div>

                        {/* 1. Global Post Creation Toggle */}
                        <div className="border border-light-border dark:border-dark-border rounded-xl p-4">
                            <div className="flex items-start justify-between gap-4">
                                <div className="flex-1">
                                    <div className="flex items-center gap-2 mb-2">
                                        <h3 className="font-semibold text-light-text dark:text-dark-text">
                                            Allow Users to Create Posts
                                        </h3>
                                    </div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Enable regular users to create posts. If disabled, only admins and tagged users can post.
                                    </p>
                                </div>
                                <div className="flex flex-col items-end gap-2">
                                    <button
                                        onClick={() => handleToggle(togglePostCreation, globalPostCreationEnabled, 'postCreation')}
                                        disabled={isUpdating}
                                        className={`relative inline-flex h-8 w-14 items-center rounded-full transition-colors ${globalPostCreationEnabled ? 'bg-green-500' : 'bg-gray-300 dark:bg-gray-600'
                                            }`}
                                    >
                                        <span className={`inline-block h-6 w-6 transform rounded-full bg-white transition-transform ${globalPostCreationEnabled ? 'translate-x-7' : 'translate-x-1'
                                            }`} />
                                    </button>
                                    <span className="text-xs font-medium text-light-muted dark:text-dark-muted">
                                        {globalPostCreationEnabled ? 'ON' : 'OFF'}
                                    </span>
                                </div>
                            </div>
                        </div>

                        {/* 2. Global Comments Toggle */}
                        <div className="border border-light-border dark:border-dark-border rounded-xl p-4">
                            <div className="flex items-start justify-between gap-4">
                                <div className="flex-1">
                                    <div className="flex items-center gap-2 mb-2">
                                        <h3 className="font-semibold text-light-text dark:text-dark-text">
                                            Enable Comments Globally
                                        </h3>
                                    </div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Allow users to comment on posts. Turning this off hides the comment section on all posts.
                                    </p>
                                </div>
                                <div className="flex flex-col items-end gap-2">
                                    <button
                                        onClick={() => handleToggle(toggleComments, commentsEnabled, 'comments')}
                                        disabled={isUpdating}
                                        className={`relative inline-flex h-8 w-14 items-center rounded-full transition-colors ${commentsEnabled ? 'bg-green-500' : 'bg-gray-300 dark:bg-gray-600'
                                            }`}
                                    >
                                        <span className={`inline-block h-6 w-6 transform rounded-full bg-white transition-transform ${commentsEnabled ? 'translate-x-7' : 'translate-x-1'
                                            }`} />
                                    </button>
                                    <span className="text-xs font-medium text-light-muted dark:text-dark-muted">
                                        {commentsEnabled ? 'ON' : 'OFF'}
                                    </span>
                                </div>
                            </div>
                        </div>

                        {/* 3. Question Icon on Posts Toggle */}
                        <div className="border border-light-border dark:border-dark-border rounded-xl p-4">
                            <div className="flex items-start justify-between gap-4">
                                <div className="flex-1">
                                    <div className="flex items-center gap-2 mb-2">
                                        <h3 className="font-semibold text-light-text dark:text-dark-text">
                                            Enable "Ask Question" on Posts
                                        </h3>
                                    </div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Allow users to ask questions directly on teaching posts.
                                    </p>
                                </div>
                                <div className="flex flex-col items-end gap-2">
                                    <button
                                        onClick={() => handleToggle(toggleQuestionsOnPosts, questionsOnPostsEnabled, 'questionsOnPosts')}
                                        disabled={isUpdating}
                                        className={`relative inline-flex h-8 w-14 items-center rounded-full transition-colors ${questionsOnPostsEnabled ? 'bg-green-500' : 'bg-gray-300 dark:bg-gray-600'
                                            }`}
                                    >
                                        <span className={`inline-block h-6 w-6 transform rounded-full bg-white transition-transform ${questionsOnPostsEnabled ? 'translate-x-7' : 'translate-x-1'
                                            }`} />
                                    </button>
                                    <span className="text-xs font-medium text-light-muted dark:text-dark-muted">
                                        {questionsOnPostsEnabled ? 'ON' : 'OFF'}
                                    </span>
                                </div>
                            </div>
                        </div>

                        {/* 4. Chat System Toggle */}
                        <div className="border border-light-border dark:border-dark-border rounded-xl p-4">
                            <div className="flex items-start justify-between gap-4">
                                <div className="flex-1">
                                    <div className="flex items-center gap-2 mb-2">
                                        <MessageCircle className="w-5 h-5 text-primary-500" />
                                        <h3 className="font-semibold text-light-text dark:text-dark-text">
                                            Enable Chat System
                                        </h3>
                                    </div>
                                    <p className="text-sm text-light-muted dark:text-dark-muted">
                                        Allow users to chat with each other and admins. If disabled, only admins can initiate chats.
                                    </p>
                                </div>
                                <div className="flex flex-col items-end gap-2">
                                    <button
                                        onClick={() => handleToggle(toggleChat, chatEnabled, 'chat')}
                                        disabled={isUpdating}
                                        className={`relative inline-flex h-8 w-14 items-center rounded-full transition-colors ${chatEnabled ? 'bg-green-500' : 'bg-gray-300 dark:bg-gray-600'
                                            }`}
                                    >
                                        <span className={`inline-block h-6 w-6 transform rounded-full bg-white transition-transform ${chatEnabled ? 'translate-x-7' : 'translate-x-1'
                                            }`} />
                                    </button>
                                    <span className="text-xs font-medium text-light-muted dark:text-dark-muted">
                                        {chatEnabled ? 'ON' : 'OFF'}
                                    </span>
                                </div>
                            </div>
                        </div>

                    </motion.div>

                    {/* Info Card */}
                    <motion.div
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: 0.1 }}
                        className="glass rounded-2xl p-6 shadow-lg border-l-4 border-primary-500"
                    >
                        <h3 className="font-bold text-light-text dark:text-dark-text mb-2">
                             How it works
                        </h3>
                        <ul className="space-y-2 text-sm text-light-muted dark:text-dark-muted">
                            <li className="flex items-start gap-2">
                                <span className="text-primary-500 mt-1"></span>
                                <span>
                                    <strong>Global ON:</strong> Every user (even new signups) can immediately create question and prayer posts.
                                </span>
                            </li>
                            <li className="flex items-start gap-2">
                                <span className="text-primary-500 mt-1"></span>
                                <span>
                                    <strong>Global OFF:</strong> Users need to be individually tagged by you before they can create posts.
                                </span>
                            </li>
                            <li className="flex items-start gap-2">
                                <span className="text-primary-500 mt-1"></span>
                                <span>
                                    <strong>Admins:</strong> Always have permission to create posts, regardless of this setting.
                                </span>
                            </li>
                            <li className="flex items-start gap-2">
                                <span className="text-primary-500 mt-1"></span>
                                <span>
                                    <strong>Questions on Posts:</strong> Controls whether the 'Ask Question' button appears on official posts.
                                </span>
                            </li>
                            <li className="flex items-start gap-2">
                                <span className="text-primary-500 mt-1"></span>
                                <span>
                                    <strong>Chat System:</strong> Controls whether users can initiate chats. Admins can always chat regardless.
                                </span>
                            </li>
                        </ul>
                    </motion.div>
                </div>
            </div>
        </div>
    );
}
