import { useState } from 'react';
import { Modal } from '../ui/Modal';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { useMutation } from 'convex/react';
import { api } from '../../../convex/_generated/api';
import { useToast } from '@/context/ToastContext';
import { Calendar, Image, X, Upload } from 'lucide-react';

interface SubmitPaymentModalProps {
    isOpen: boolean;
    onClose: () => void;
    user: any;
}

export function SubmitPaymentModal({ isOpen, onClose, user }: SubmitPaymentModalProps) {
    const [amount, setAmount] = useState('1000');
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [year, setYear] = useState(new Date().getFullYear());
    const [proofFile, setProofFile] = useState<File | null>(null);
    const [isUploading, setIsUploading] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const generateUploadUrl = useMutation(api.files.generateUploadUrl);
    const submitPayment = useMutation(api.finance.submitPaymentSubmission);
    const { showToast } = useToast();

    const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        // Validate file type
        if (!file.type.startsWith('image/')) {
            showToast('error', 'Please upload an image file');
            return;
        }

        // Validate file size (max 5MB)
        if (file.size > 5 * 1024 * 1024) {
            showToast('error', 'File size must be less than 5MB');
            return;
        }

        setProofFile(file);
    };

    const handleSubmit = async () => {
        if (!proofFile) {
            showToast('error', 'Please upload a proof of payment');
            return;
        }

        setIsSubmitting(true);
        try {
            // 1. Get upload URL
            setIsUploading(true);
            const uploadUrl = await generateUploadUrl();

            // 2. Upload to storage
            const result = await fetch(uploadUrl, {
                method: "POST",
                headers: { "Content-Type": proofFile.type },
                body: proofFile,
            });
            const { storageId } = await result.json();
            setIsUploading(false);

            // 3. Submit request
            await submitPayment({
                userId: user._id,
                amount: parseFloat(amount),
                month,
                year,
                proofStorageId: storageId,
            });

            showToast('success', 'Payment submission sent for approval!');
            handleClose();
        } catch (error: any) {
            console.error('Failed to submit payment:', error);
            showToast('error', error.message || 'Failed to submit payment');
        } finally {
            setIsUploading(false);
            setIsSubmitting(false);
        }
    };

    const handleClose = () => {
        setAmount('1000');
        setMonth(new Date().getMonth() + 1);
        setYear(new Date().getFullYear());
        setProofFile(null);
        onClose();
    };

    return (
        <Modal isOpen={isOpen} onClose={handleClose} title="Submit Board Due Payment">
            <div className="space-y-6">
                <div className="p-4 rounded-2xl bg-primary-500/5 border border-primary-500/10 flex items-center gap-4">
                    <div className="w-12 h-12 rounded-full bg-primary-500/10 flex items-center justify-center shrink-0">
                        <Calendar className="w-6 h-6 text-primary-500" />
                    </div>
                    <div>
                        <p className="text-xs font-bold text-light-muted uppercase">Paying For</p>
                        <p className="font-bold text-light-text dark:text-dark-text">
                            {new Date(year, month - 1).toLocaleDateString('en-US', { month: 'long', year: 'numeric' })}
                        </p>
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                        <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">Amount ()</label>
                        <Input
                            type="number"
                            value={amount}
                            onChange={(e) => setAmount(e.target.value)}
                            placeholder="1000"
                            className="font-bold text-lg"
                        />
                    </div>
                    <div className="grid grid-cols-2 gap-2">
                        <div>
                            <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">Month</label>
                            <select
                                value={month}
                                onChange={(e) => setMonth(Number(e.target.value))}
                                className="w-full px-4 py-2 rounded-xl border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-sm font-bold"
                            >
                                {Array.from({ length: 12 }, (_, i) => (
                                    <option key={i + 1} value={i + 1}>
                                        {new Date(2024, i).toLocaleDateString('en-US', { month: 'short' })}
                                    </option>
                                ))}
                            </select>
                        </div>
                        <div>
                            <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">Year</label>
                            <select
                                value={year}
                                onChange={(e) => setYear(Number(e.target.value))}
                                className="w-full px-4 py-2 rounded-xl border border-light-border dark:border-dark-border bg-light-bg dark:bg-dark-bg text-sm font-bold"
                            >
                                {[2024, 2025, 2026].map(y => (
                                    <option key={y} value={y}>{y}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-bold mb-2 uppercase tracking-wide text-light-muted">Payment Proof (Screenshot)</label>
                    <div className="space-y-4">
                        {!proofFile ? (
                            <label className="flex flex-col items-center justify-center w-full h-40 border-2 border-dashed border-light-border dark:border-dark-border rounded-2xl hover:border-primary-500 cursor-pointer bg-light-bg/30 dark:bg-dark-bg/30 transition-all">
                                <div className="flex flex-col items-center justify-center pt-5 pb-6">
                                    <div className="p-3 rounded-full bg-primary-500/10 text-primary-500 mb-3">
                                        <Upload className="w-6 h-6" />
                                    </div>
                                    <p className="text-sm font-bold text-light-text dark:text-dark-text">Click to upload proof</p>
                                    <p className="text-xs text-light-muted">PNG or JPG (Max 5MB)</p>
                                </div>
                                <input type="file" className="hidden" accept="image/*" onChange={handleFileChange} />
                            </label>
                        ) : (
                            <div className="relative group rounded-2xl overflow-hidden border border-light-border dark:border-dark-border bg-black/5">
                                <img
                                    src={URL.createObjectURL(proofFile)}
                                    alt="Proof Preview"
                                    className="w-full h-48 object-contain"
                                />
                                <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                    <button
                                        onClick={() => setProofFile(null)}
                                        className="p-3 rounded-full bg-rose-500 text-white shadow-lg transform hover:scale-110 transition-transform"
                                    >
                                        <X className="w-6 h-6" />
                                    </button>
                                </div>
                                <div className="p-3 bg-light-bg dark:bg-dark-bg border-t border-light-border dark:border-dark-border flex items-center justify-between">
                                    <div className="flex items-center gap-2">
                                        <Image className="w-4 h-4 text-light-muted" />
                                        <span className="text-xs font-bold truncate max-w-[150px]">{proofFile.name}</span>
                                    </div>
                                    <span className="text-[10px] text-light-muted">{(proofFile.size / 1024 / 1024).toFixed(2)} MB</span>
                                </div>
                            </div>
                        )}
                    </div>
                </div>

                <div className="pt-4 flex gap-4">
                    <Button
                        variant="ghost"
                        className="flex-1 rounded-2xl h-12"
                        onClick={handleClose}
                        disabled={isSubmitting}
                    >
                        Cancel
                    </Button>
                    <Button
                        className="flex-1 rounded-2xl h-12 bg-primary-500 hover:bg-primary-600 shadow-lg shadow-primary-500/20"
                        onClick={handleSubmit}
                        disabled={isSubmitting || isUploading || !proofFile}
                    >
                        {isUploading ? 'Uploading...' : isSubmitting ? 'Submitting...' : 'Submit Payment'}
                    </Button>
                </div>
            </div>
        </Modal>
    );
}
